.Accueil-h3{
    font-family: "Poiret One", serif;
    font-weight: lighter;
    margin: 10px 20px;
    color: #13435b;
}

.Accueil-p{
    font-family: "Roboto", sans-serif;
    font-weight: normal;
    margin: 10px 0;
    text-indent: 30px;
    text-align: justify;
}

.Accueil-titre{
    display: flex;
    align-items: center;
}

#AccueilCAS-CAS{
    margin-top: 20px;
    width: 100%;
}
#AccueilTeam-fleche{
    transform: rotate(180deg);
}
#AccueilTeam-titre{justify-content: flex-end;}

@media only screen and (min-width: 768px) {
    .Accueil-fleche{height: 200px;}
    #AccueilTeam-titre{margin-right: -100px;}
    #AboutUs-titre, #AccueilCAS-titre{margin-left:-100px}
    .Accueil-texte{margin: 100px;}
    .Accueil-p{font-size: 20px;}
    .Accueil-h3{font-size: 70px;}
}

@media only screen and (max-width: 767px) {
    .Accueil-fleche{height: 75px;}
    #AccueilTeam-titre{margin-right: -20px;}
    #AboutUs-titre, #AccueilCAS-titre{margin-left:-20px}
    .Accueil-texte{margin: 20px;}
    .Accueil-p{font-size: 18px;}
    .Accueil-h3{font-size: 30px;}
}