#AccueilTeamOrganigramme-Container{
    display: grid;
    grid-template-areas:
        "a a a j j j"
        "a a a b b b"
        "c c c b b b"
        "c c c d d d"
        "e e e d d d"
        "e e e f f f"
        "g g g f f f"
        "g g g h h h"
        "i i i h h h"
        "i i i k k k";
    grid-gap: 50px;
    justify-content: center;
}

#UaAdministrateurJulieBRUANT{grid-area: a;}
#UaAdministrateurAudreyPHILIPPE{grid-area: b;}
#UaAdministrateurJulieBRUNETTO{grid-area: c;}
#UaAdministrateurBaptisteREDON{grid-area: d;}
#UaAdministrateurPaulPETIT{grid-area: e;}
#UaAdministrateurSarahHONORÉ{grid-area: f;}
#UaAdministrateurMorganeROY{grid-area: g;}
#UaAdministrateurAdrienCOULLOUMME{grid-area: h;}
#UaAdministrateurAudrenBUTERY{grid-area: i;}

@media only screen and (min-width: 768px) {
    #AccueilTeamOrganigramme-Container {
        padding-top: 40px;
        padding-bottom: 40px;
    }
}

@media only screen and (max-width: 767px) {
    #AccueilTeamOrganigramme-Container{
        transform: scale(0.325);
        padding-top: 30px;
        padding-bottom: 30px;
        margin-top: -385px;
        margin-bottom: -385px;
    }
}